export default {
    props: {
        data: {
            type: Object,
            default () {
                return {}
            },
        },
    },
    methods: {
        capitalizeFirstLetter (string) {
            return string?.toLowerCase().charAt(0).toUpperCase() + string?.slice(1)
        },
    },
}
